<template>
    <div class="container mr-auto">
        <div class="jumbotron text-center">
            <h1></h1>
        </div>
        
        <hr>
        <br>
        <h2> </h2>
        <table class="table">
                    <colgroup>
                            <col width = "*">
                            <col width = "*">
                            <col width = "15%">
                            <col width = "10%">
                            <col width = "*">
                            <col width = "*">
                            <col width = "*">
                            <col width = "10%">
                        </colgroup>
            <thead>
                <tr>
                <th></th>
                <th></th>
                <th></th>
                <th id="th3" scope="col"></th>
                <th id="th2" scope="col"></th>
                <th id="th3" scope="col"></th>
                <th id="th3" scope="col"></th>
                <th></th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in myPageOrders" v-bind:key="item.orderPk">
                    <th id="th1" scope="row">
                        <!--210902   -->
                        <img
                            id="myimg"
                            alt="localhost9000"
                            style=""
                            v-bind:src="item.storedFilePath"
                            data-holder-rendered="true"
                        />
                    </th>
                    <td>{{ item.orderPk }}</td>
                    <td>{{ item.productName }}</td>
                    <td>{{ item.productStore }}</td>
                    <td>{{ item.productPrice }}</td>
                    <td>{{ item.orderCnt }} </td>
                    <td>{{ item.orderSum }}</td>
                    <td>
                        <button type="button" class="btn btn" id="btnRe" v-on:click= "moveReviewWrite(item.productPk)">  </button>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
export default {
    data() {
        return {
            myPageOrders: [],
            customerPk: 0,
        }
    },
    methods: {
        moveReviewWrite(productPk) {
            this.$router.push({ name: 'ReviewWrite', query: { productPk: productPk }});
        },
    },
    mounted() {
        let obj = this;

        // obj.$axios.get("http://localhost:9000/myPageOrder", {
        obj.$axios.get("http://18.222.233.235:9000/myPageOrder", {
                params: {
                    // customerPk: customerPk, //      
                    customerPk: sessionStorage.getItem("customerPk")
                },
            })
            .then(function (res) {
                console.log("axios   ");
                obj.myPageOrders = res.data;
            })
            .catch(function (err) {
                console.log("axios   ");
                console.log(err);
            });
    },
}
</script>

<style scoped>
#myimg {
    width: 100px;
    height: 100px;
    border-radius: 100px;
}
td {
    text-align: center;
    vertical-align: middle;
}
th {
    text-align: center;
    vertical-align: middle;
}
#th1 {
    width: 100px;
}
#th2, #th3, #th4 {
    width: 150px;
}
#th5 {
    width: 150px;
}
#tdbottom {
    border-bottom: 0.8px solid lightgray;
}

#routerlink {
    color: white;
}
</style>