<template>
    <div class="container mr-auto">
        <div class="jumbotron text-center">
            <h1> </h1>
        </div>
        <hr>
        <div class="container" id="noticewri">
            <form>
                <div class="form-group row">
                    <label for="text" class="col-sm-2 col-form-label"></label>
                    <div class="col-sm-10">
                        <input type="text" class="form-control" id="exampleFormControlInput1" placeholder=" " v-model="noticeTitle">
                    </div>
                </div>
                <div class="form-group row">
                    <label for="text" class="col-sm-2 col-form-label"></label>
                    <div class="col-sm-10">
                        <input type="text" class="form-control-plaintext" id="exampleFormControlInput1" readonly  placeholder=" " v-model="createId">
                    </div>
                </div>

                <div class="form-group row">
                    <label for="text" class="col-sm-2 col-form-label"></label>
                    <div class="col-sm-10">
                        <textarea class="form-control" id="exampleFormControlTextarea1" rows="8" v-model="noticeContents"></textarea>
                    </div>
                </div>

                <div class="form-group row">
                <label for="text" class="col-sm-2 col-form-label"></label>
                <input type="file" class="form-control-file col" id="exampleFormControlFile1" @change="selectFile" ref="boardImg">
                </div>

                <div class="row ">
                    <button type="button" class="btn btn col-sm-1 ml-auto" id="btnList" v-on:click = "moveAdminNoticeList"></button>
                    <button type="button" class="btn btn col-sm-1 " id="btnwrite" v-on:click = "AdminNoticeWrite"></button>
                </div>
            </form> 
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            // noticePk:0,
            boardCategoryPk:'b1',
            noticeTitle:'',
            createId:'admin',
            noticeContents:'',
            image:'',
        }
    },
    methods: {
        selectFile() {
                    this.image = this.$refs.boardImg.files[0];
                },
        //axios   
        AdminNoticeWrite() {
            let obj = this;

            const formData = new FormData();

            // formData.append('productPk', this.productPk);
            formData.append('boardCategoryPk', this.boardCategoryPk)
            // formData.append('noticePk', this.noticePk)
            formData.append('noticeTitle', this.noticeTitle)
            formData.append('createId', this.createId)
            formData.append('noticeContents', this.noticeContents)
            formData.append('image', this.image);

            // obj.$axios.post('http://localhost:9000/noticeWrite', formData, {
            obj.$axios.post('http://18.222.233.235:9000/noticeWrite', formData, {
                headers: {
                    'Content-Type': 'multipart/form-data'
                }
            })
            .then(function() {
                console.log("  ");
                obj.$router.push({name:'AdminNoticeList'});
                
            })
            .catch(function(err) {
                console.log("  ");
                console.log(err);
            })
        },
        //  
        moveAdminNoticeList() {
            this.$router.push({ name: 'AdminNoticeList'});
        }
    },
    
}
</script>

