<template>
    <div class="container mr-auto">
        <div class="jumbotron text-center">
            <h1></h1>
        </div>
        <hr>
        <!--    -->
        <table class="table">
            <thead>
                <tr>
                <th></th>
                <th></th>
                <th id="th3" scope="col"></th>
                <th id="th2" scope="col"></th>
                <th id="th3" scope="col"></th>
                <th id="th3" scope="col"></th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in cartLists" v-bind:key="item.customerPk">
                    <th id="th1" scope="row">
                            <img id="myimg"
                            alt="localhost9000"
                            style=""
                            v-bind:src="item.storedFilePath"
                            data-holder-rendered="true"
                        />
                    </th>
                    <td>{{ item.productName }}</td>
                    <td>{{ item.productStore }}</td>
                    <td>{{ item.productPrice }}</td>
                    <td>{{ item.orderCnt }} </td>
                    <td>{{ item.orderSum }}</td>
                </tr>
            </tbody>
        </table>

        <hr>
        <br>
        <!--    -->
        <div class="container">
            <form>
                <div>
                    <div class="form-group">
                    <label for="address"><b></b></label>
                        <div class="daummap">
                            <div class="form-group row">
                                <div class="col">
                                    <div class="row">
                                        <label for="zip" class="col-sm-4 col-form-label"></label>
                                        <input type="text" class="form-control col" id="zip" name="zip" placeholder="" v-model="zip">
                                    </div>
                                </div>
                                <div class="col">
                                    <button class="btn btn-secondary" @click="showApi"></button> </div>
                                </div>
                            </div>
                            <div class="form-group row">
                                <div class="col">
                                    <div class="row">
                                        <label for="addr1" class="col-sm-4 col-form-label"></label>
                                        <input type="text" class="form-control col" id="addr1" name="addr1" placeholder="" v-model="addr1">
                                    </div>
                                </div>
                                <div class="col">
                                    <div class="row">
                                        <label for="addr2" class="col-sm-3 col-form-label"></label>
                                        <input type="text" class="form-control col" id="addr2" name="addr2" placeholder=" " v-model="addr2">
                                    </div>
                                </div>
                            </div>
                    </div>
                </div>
                <div class="form-group">
                    <label for="orderName"><b> </b></label>
                    <input type="text" class="form-control" id="orderName" name="orderName" placeholder=" " v-model="orderName">
                </div>
                <div class="form-group">
                    <label for="orderPhone"><b> </b></label>
                    <input type="text" class="form-control" id="orderPhone" name="orderPhone" placeholder="  " v-model="orderPhone">
                </div>
                <div>
                    <label for="orderAsk"><b></b></label>
                    <!-- <select class="form-control" id="orderAsk" name="orderAsk" v-model="orderAsk"> 
                        <option selected>Open this select menu</option> 
                        <option value="1"> </option>
                        <option value="2"> </option>
                        <option value="3">  </option>
                        <option value="4">  </option>
                    </select> -->

                    <select class="custom-select custom-select-sm" v-model="orderAsk">
                        <option selected> </option>
                        <option value="1"> </option>
                        <option value="2">  </option>
                        <option value="3">  </option>
                        <option value="4"> </option>
                    </select>
                </div>
                
                <br>
                <!-- ,    -->
                <div class="container">
                    <div class="row">
                        <div class="col-sm">
                            <p><b></b></p>
                            <div class="col-sm-10">
                            <div class="form-check">
                                <input class="form-check-input" type="radio" name="gridRadios" id="gridRadios1" value="option1" checked>
                                <label class="form-check-label" for="gridRadios1">
                                 / 
                                </label>
                            </div>
                            <div class="form-check">
                                <input class="form-check-input" type="radio" name="gridRadios" id="gridRadios2" value="option2">
                                <label class="form-check-label" for="gridRadios2">
                                
                                </label>
                            </div>
                            <div class="form-check">
                                <input class="form-check-input" type="radio" name="gridRadios" id="gridRadios3" value="option3">
                                <label class="form-check-label" for="gridRadios3">
                                
                                </label>
                            </div>
                            <div class="form-check">
                                <input class="form-check-input" type="radio" name="gridRadios" id="gridRadios4" value="option4">
                                <label class="form-check-label" for="gridRadios4">
                                
                                </label>
                            </div>
                        </div>
                        </div>
                        <div class="col-sm">
                            <p><b></b></p>
                            <div class="row">
                                <div class="col"></div> 
                                <div class="col">{{ totalAdd() }}</div>
                            </div>
                            <div class="row">
                                <div class="col"></div> 
                                <div class="col">2500</div> 
                            </div>
                            <hr>
                            <div class="row">
                            <div class="col"><h5> </h5></div>
                            <div class="col"><h5>{{ totalAddDelivery() }}</h5></div>
                            </div>
                            
                        </div>
                    </div>
                </div>
                <hr>
                <button type="button" class="btn btn-warning btn-lg" v-on:click="orderInsert"></button>

                </form>
        </div>
    </div>
</template>

<script>
export default {
    name: 'daumMap',
    data() { 
        return { 
            zip: "", 
            addr1: "", 
            addr2: "",
            orderName: "",
            orderPhone: "",
            orderDate: "",
            orderAsk: 0,
            totalPrice: "",
            totalPriceDelivery: "",
            orderProducts: [],
            cartLists: [],
            customerPk: 0,
            } 
    }, 
    methods: {
        showApi() { 
            new window.daum.Postcode({ 
                oncomplete: (data) => { 
                    //        . 
                    
                    //       . 
                    //      ('') ,    . 
                    let fullRoadAddr = data.roadAddress; //    
                    let extraRoadAddr = ''; //     
                    
                    //    . ( ) 
                    //     "//" . 
                    if(data.bname !== '' && /[||]$/g.test(data.bname)){ 
                        extraRoadAddr += data.bname; 
                        } 
                    //  ,   . 
                    if(data.buildingName !== '' && data.apartment === 'Y'){
                        extraRoadAddr += (extraRoadAddr !== '' ? ', ' + data.buildingName : data.buildingName); 
                        } 
                    
                    // ,     ,     . 
                    
                    if(extraRoadAddr !== ''){ 
                        extraRoadAddr = ' (' + extraRoadAddr + ')'; 
                        } 
                    
                    // ,        . 
                    if(fullRoadAddr !== ''){ 
                        fullRoadAddr += extraRoadAddr; 
                        } 
                    
                    //      . 
                    this.zip = data.zonecode; //5    
                    this.addr1 = fullRoadAddr; 
                } 
            }).open()
        },
        orderProduct(orderPk) {
            this.$router.push({
                name: "Order",
                params: { orderPk: orderPk },
            });
        },
        orderInsert() {
            let obj = this;
            // obj.$axios.post('http://localhost:9000/orderInsert', {
            obj.$axios.post('http://18.222.233.235:9000/orderInsert', {
                orderName: this.orderName,
                orderPhone: this.orderPhone,
                zip: this.zip,
                addr1: this.addr1,
                addr2: this.addr2,
                orderDate: this.orderDate,
                orderAsk: this.orderAsk,
                totalPrice: this.totalPrice,
                totalPriceDelivery: this.totalPriceDelivery,
                customerPk: sessionStorage.getItem("customerPk"),
            })
            .then(function() {
                console.log('  y ');
                alert(" ");
                obj.$router.push({ name: 'Index' });
            })

            .catch(function(err) {
                console.log("  ");
                console.log(err);
            });
        },
        totalAdd() {
            let total = 0;
            if (this.cartLists.length > 0) {
                for (let i = 0; i < this.cartLists.length; i++) {
                    total += this.cartLists[i].orderSum;
                }
            }
            this.totalPrice = total;
            return this.totalPrice;
        },
        totalAddDelivery() {
            let total = 0;
            if (this.cartLists.length > 0) {
                for (let i = 0; i < this.cartLists.length; i++) {
                    total += this.cartLists[i].orderSum;
                }
            }
            this.totalPriceDelivery = total + 2500;
            return this.totalPriceDelivery;
        }
    },
    mounted() {
        let obj = this;

        obj.$axios
            // .get("http://localhost:9000/cartList", {
            .get("http://18.222.233.235:9000/cartList", {
                params: {
                    customerPk: sessionStorage.getItem("customerPk"), //      
                },
            })
            .then(function (res) {
                console.log("axios   ");
                obj.cartLists = res.data;
            })
            .catch(function (err) {
                console.log("axios   ");
                console.log(err);
            });
    }
}
</script>

<style scoped>
#myimg {
    width: 100px;
    height: 100px;
    border-radius: 100px;
}
td {
    text-align: center;
    vertical-align: middle;
}
th {
    text-align: center;
    vertical-align: middle;
}
#th1 {
    width: 100px;
}
#th2, #th3, #th4 {
    width: 150px;
}
#tdbottom {
    border-bottom: 0.8px solid lightgray;
}
table {
    width: 1000px;
    margin: auto;
}
</style>