<template>
    <div class="container">
        <div class="jumbotron text-center">
            <h1> </h1>
        </div>
        <hr>
        <!--    -->
        <table class="table">
            <thead>
                <tr>
                    <th class="column-1"></th>
                    <th class="column-2"></th>
                    <th class="column-6"></th>
                    <th class="column-3"></th>
                    <th class="column-4"></th>
                    <th class="column-5"></th>
                    <th class="column-6"></th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in cartLists" v-bind:key="item.customerPk">
                    <td class="column-1">
                        <img
                            id="myimg"
                            alt="localhost9000"
                            style=""
                            v-bind:src="item.storedFilePath"
                            data-holder-rendered="true"
                        />
                        <!-- <div class="cart-img-product" @click="delItem(item.id)">
                            <img :src="item.image" alt="IMG-PRODUCT">
                              ,    
                        </div> -->
                    </td>
                    <td class="column-2">{{ item.productName }}</td>
                    <td class="column-6">{{ item.productStore }}</td>
                    <td class="column-3">{{ item.productPrice }}</td>
                    <td class="column-4">{{ item.orderCnt }}</td>
                    <td class="column-5">{{ item.orderSum }}</td>
                    <td class="column-6">
                    <button type="button" class="close" aria-label="Close" v-on:click="cartDelete(item.orderDetailPk)">
                        <span aria-hidden="true">&times;</span>
                    </button>
                    </td>

                </tr>
            </tbody>
        </table>
        <br>
        <button type="button" class="btn btn-success btn-lg btn-block" v-on:click="moveOrder"></button>
    </div>
</template>

<script>
export default {
    data() {
        return {
            orderDetailPk: 0,
            cartLists: [],
            customerPk: 0
        };
    },
    methods: {
        moveOrder() {
            this.$router.push({
                name: "Order",
                query: { customerPk: sessionStorage.getItem("customerPk") },
            });
        },
        cartDelete(orderDetailPk) {
            let obj = this;
            // obj.orderDetailPk = obj.$route.query.orderDetailPk;

            // this.$axios.delete('http://localhost:9000/cartDelete', {
            this.$axios.delete('http://18.222.233.235:9000/cartDelete', {
                params: {
                    orderDetailPk: orderDetailPk
                }
            })
            .then(function() {
                console.log("  ");
                obj.$router.go(obj.$router.currentRouter);
            })
            .catch(function(err) {
                console.log("  ");
                console.log(err);
            });
        }
    },
    mounted() {
        let obj = this;
        // obj.$axios.get("http://localhost:9000/cartList", {
        obj.$axios.get("http://18.222.233.235:9000/cartList", {
                params: {
                    customerPk: sessionStorage.getItem("customerPk"), //      
                },
            })
            .then(function (res) {
                console.log("axios   ");
                obj.cartLists = res.data;
            })
            .catch(function (err) {
                console.log("axios   ");
                console.log(err);
            });
    }
};
</script>

<style scoped>
#myimg {
    width: 100px;
    height: 100px;
    border-radius: 100px;
}
td {
    text-align: center;
    vertical-align: middle;
}
th {
    text-align: center;
    vertical-align: middle;
}

table {
    width: 1000px;
    margin: 50px auto 100px auto;
}
button {
    margin-bottom: 50px;
}
</style>