<template>
    <div class="container">
        <div class="jumbotron text-center">
            <h1></h1>
        </div>
        <div class="container" id="joinform">
            <form>
                <div class="form-group">
                    <label for="customerName"></label>
                    <input type="text" class="form-control" id="customerName" aria-describedby="nameHelp" placeholder=" " v-model="customerName">
                </div>

                <div class="form-group">
                    <label for="customerPhone"></label>
                    <input type="tel" class="form-control" id="customerPhone" aria-describedby="phoneHelp" placeholder="-   " v-model="customerPhone">
                </div>

                <div class="form-group">
                    <label for="customerEmail"></label>
                    &nbsp;&nbsp;<button type="button" class="btn btn-sm" id="btnidcheck" v-on:click="idCheck">  </button>
                    <input type="email" class="form-control" id="customerEmail" aria-describedby="emailHelp" placeholder=" " v-model="customerEmail">
                </div>

                <div class="form-group">
                    <label for="customerPw"></label>
                    <input type="password" class="form-control" id="customerPw" placeholder="******" v-model="customerPw">
                </div>

                <div class="form-group">
                    <label for="customerPw"> </label>
                    <input type="password" class="form-control" id="customerPw" placeholder="******">
                </div>

                <div class="form-group form-check">
                    <input type="checkbox" class="form-check-input" id="exampleCheck1">
                    <label class="form-check-label" for="exampleCheck1">   .</label>
                </div>

                <button type="button" class="btn btn" id="btnjoin" v-on:click="join"></button>
            </form>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            customerEmail: '',
            customerPw: '',
            customerName: '',
            customerPhone: '',
            adminYn: '',
            errors: [],
        }
    },
    methods: {
        validationCheck() {
            this.errors = [];
            if (this.customerEmail == '') {
                this.errors.push(" .");
            }

            if (this.customerPw == '') {
                this.errors.push(" .");
            }

            if (this.customerName == '') {
                this.errors.push(" .");
            }

            if (this.customerPhone == '') {
                this.errors.push(" .");
            }

            if (this.errors.length > 0) {
                return true;
            }
            else {
                return false;
            }
        },

        join() {
            let obj = this;

            if (this.validationCheck() == false) {

                // obj.$axios.post('http://localhost:9000/join', {
                obj.$axios.post('http://18.222.233.235:9000/join', {
                customerEmail: this.customerEmail,
                customerPw: this.customerPw,
                customerName: this.customerName,
                customerPhone: this.customerPhone,
                adminYn: this.adminYn
                })
                .then(function() {
                    console.log('  ');
                    obj.$router.push({ name: 'Main' });
                        alert(" ! .");
                })
                .catch(function(err) {
                    console.log("  ");
                    console.log(err);
                });
            }
            else {
                let errorResult = '';
                for (let i = 0; i < this.errors.length; i++) {
                    errorResult += this.errors[i] + '\n';
                }

                alert(errorResult);
            }
        },
        idCheck() {
            let obj = this;

            let emailVal = this.customerEmail;
            let regExp = /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i; //     regExp 
            if (emailVal.match(regExp) != null) {
                // obj.$axios.get('http://localhost:9000/idCheck', {
                obj.$axios.get('http://18.222.233.235:9000/idCheck', {
                    params: {
                        customerEmail: this.customerEmail
                    }
                })
                .then(function(res) {
                    // console.log('  ');

                    if (res.data.result == true) {
                        alert(" .");
                    }
                    else {
                        alert("  .");
                    }
                })
                .catch(function(err) {
                    console.log("  ");
                    console.log(err);
                });
            }
            else {
                alert('  ');
            }
        }
    },
}
</script>

<style scoped>
#joinform {
    margin-top: -50px;
    width: 70%;
    /* border: 1px solid lightgray; */
    border-radius: 20px;
    padding: 50px;
}
#btnjoin {
    background-color: rgb(93, 141, 91);
    color: white;
    border-radius: 50px;
}

#btnidcheck {
    background-color: rgb(106, 110, 105);
    border-radius: 50px;
    color: white;
}
</style>